@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;width:100vw;height:100vh;background-color:blanchedalmond;position:relative;box-sizing:border-box;display:flex;flex-wrap:wrap;flex-flow:column wrap;align-items:center;justify-content:center}h2:not(:first-child){margin-top:2rem}/*!
 * "JetBrains Mono" is lisenced under the SIL Open Font License 1.1
 * by https://fonts.google.com/
 */.Info{color:white;font-size:3rem;font-family:'JetBrains Mono', monospace;display:flex;flex-flow:column;flex:none;z-index:10}.Info>span{display:block;margin:2rem;transition:all 1s ease}.Info-btn{font-family:inherit;font-weight:bold;-webkit-appearance:none;-moz-appearance:none;appearance:none;font-size:1.5rem;margin:3rem auto;padding:2rem 3rem;border-radius:10px;background:rgba(0,0,0,0)}.Info-btn:hover{box-shadow:0px 0px 0px 2px rgba(0,0,255,0.7);cursor:-webkit-grab;cursor:grab}.Info-btn:focus{outline:none;box-shadow:0px 0px 0px 4px rgba(42,134,255,0.7)}.prev-btn{position:absolute;left:5vw;bottom:2vh;font-size:1.2rem;transition:opacity 0.3s ease-out}.next-btn{position:absolute;right:5vw;bottom:2vh;font-size:1.2rem;transition:opacity 0.3s ease-out}.Second{box-sizing:border-box;width:100vw;min-height:100vh;text-align:center;padding:0 0 2rem}.Second>.color-info{font-size:1rem;font-family:'JetBrains Mono', monospace;display:flex;flex-flow:column;flex:none}.Second>.color-info>p{-webkit-margin-before:0.5rem;margin-block-start:0.5rem;-webkit-margin-after:0rem;margin-block-end:0rem}.Second>.color-info>p:last-child{-webkit-margin-after:1rem;margin-block-end:1rem}.Second>.img-opacity>label{position:relative}.Second>.img-opacity>label::before{content:"";position:absolute;transition:all 0.1s ease-out;width:10px;height:10px;left:0;border-left:0px solid #666;border-bottom:0px solid #666}.Second>.img-opacity>label::after{content:"";opacity:0;position:absolute;width:40vw;left:0;box-shadow:10px 0px 0px #dbf0db;transition:all 0.3s ease-out}.Second>.img-opacity>label:hover::before{content:"";position:absolute;width:10px;height:10px;border-left:2px solid #666;border-bottom:2px solid #666;background:#f2f2f2;transform:translate(0.5rem, -100%) rotate(-45deg)}.Second>.img-opacity>label:hover::after{content:attr(data-tooltip);opacity:1;position:absolute;width:40vw;left:-0.8rem;transform:translate(10px, -110%);background-color:#f2f2f2;box-shadow:1px 0px 0px #dbf0db}.Second>.pref-item{padding:1.2rem 0;margin:0 0.2rem}.Second>.locate-image-wrapper{display:grid;grid-template-columns:1fr 1fr 1fr;grid-auto-rows:minmax(100px, 33%);grid-gap:1rem;min-height:500px;transition:all 0.5s ease-in}.Second>.locate-image-wrapper>.locate-selection{position:relative;display:flex;justify-content:center;align-items:center;border:solid 5px #eeeeee;border-radius:10px;cursor:pointer}.Second>.locate-image-wrapper>.locate-selection>div{box-sizing:border-box;border:solid 5px #bababa;border-radius:5px;width:50%;height:50%}.Second>.locate-image-wrapper>.selected{border:solid 5px #98b8f6;box-shadow:0px 0px 5px 1px #7198e1}.position-1{position:absolute;top:0;left:0}.position-2{position:absolute;top:0}.position-3{position:absolute;top:0;right:0}.position-4{position:absolute;left:0}.position-5{position:absolute}.position-6{position:absolute;right:0}.position-7{position:absolute;bottom:0;left:0}.position-8{position:absolute;bottom:0}.position-9{position:absolute;bottom:0;right:0}.bg-img{max-width:100vw;max-height:100vh;position:absolute}.color-history{height:200px;width:100vw;display:flex;flex-direction:row;overflow-x:scroll}.color-history>.color-item{display:flex;width:200px;flex:none;flex-direction:column;justify-content:center}input[type="range"]{-webkit-appearance:none;-moz-appearance:none;appearance:none;background-color:#c7c7c7;height:2px;width:95vw}input[type="range"]:focus,input[type="range"]:active{outline:none}input[type="range"]::-moz-range-thumb,input[type="range"]::-ms-thumb,input[type="range"]::-webkit-slider-thumb{-webkit-appearance:none;-moz-appearance:none;appearance:none;cursor:pointer;position:relative;border:none;width:12px;height:12px;display:block;background-color:#262626;border-radius:50%;-webkit-border-radius:50%}input[type="file"]+label{outline:none;border:2px solid #666;color:black;padding:5px;margin:1rem;display:inline-block;transition:all 0.1s ease-out}input[type="file"]+label:hover{background-color:#4d4d4d;color:white}.visually-hidden{position:absolute !important;height:1px;width:1px;overflow:hidden;clip:rect(1px, 1px, 1px, 1px)}input.visually-hidden:focus+label{outline:thin dotted}input.visually-hidden:focus-within+label{outline:thin dotted}.specify-imgs>.clear-btn{outline:none;border:2px solid #666;background-color:#fffbc8;color:black;padding:5px;margin:1rem;display:inline-block;transition:all 0.1s ease-out}.specify-imgs>.clear-btn:hover{border:2px solid #ef0;background-color:#fff67f}

