.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
  background-color: blanchedalmond;
  position: relative;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  flex-flow: column wrap;
  align-items: center;
  justify-content: center;
}

h2 {
  &:not(:first-child) {
    margin-top: 2rem;
  }
}

/*!
 * "JetBrains Mono" is lisenced under the SIL Open Font License 1.1
 * by https://fonts.google.com/
 */
@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@500&display=swap');
.Info {
  color: white;
  font-size: 3rem;
  font-family: 'JetBrains Mono', monospace;
  display: flex;
  flex-flow: column;
  flex: none;
  z-index: 10;
    &  > span {
    display: block;
    margin: 2rem;
    transition: all 1s ease;
  }
}

.Info-btn {
  font-family: inherit;
  font-weight: bold;
  appearance: none;
  font-size: 1.5rem;
  margin: 3rem auto;
  padding: 2rem 3rem;
  border-radius: 10px;
  background: rgba(0, 0, 0, 0);
  &:hover {
    box-shadow: 0px 0px 0px 2px rgba(0, 0, 255, .7);
    cursor: grab;
  }
  &:focus {
    outline: none;
    box-shadow: 0px 0px 0px 4px rgba(42, 134, 255, 0.7);
  }
}

.prev-btn {
  position: absolute;
  left: 5vw;
  bottom: 2vh;
  font-size: 1.2rem;
  transition: opacity 0.3s ease-out;
}

.next-btn {
  position: absolute;
  right: 5vw;
  bottom: 2vh;
  font-size: 1.2rem;
  transition: opacity 0.3s ease-out;
}

.Second {
  box-sizing: border-box;
  width: 100vw;
  min-height: 100vh;
  text-align: center;
  padding: 0 0 2rem;
  & > .color-info {
    font-size: 1rem;
    font-family: 'JetBrains Mono', monospace;
    display: flex;
    flex-flow: column;
    flex: none;
    & > p {
      margin-block-start: 0.5rem;
      margin-block-end: 0rem;
    }
    & > p:last-child {
      margin-block-end: 1rem;
    }
  }
  & > .img-opacity {
    & > label {
      position: relative;
      &::before {
        content: "";
        position: absolute;
        transition: all 0.1s ease-out;
        width: 10px;
        height: 10px;
        left: 0;
        border-left: 0px solid hsl(120, 0%, 40%);
        border-bottom: 0px solid hsl(120, 0%, 40%);
      }
      &::after {
        content: "";
        opacity: 0;
        position: absolute;
        width: 40vw;
        left: 0;
        box-shadow: 10px 0px 0px hsl(120deg, 40%, 90%);
        transition: all 0.3s ease-out;
      }
      &:hover::before {
        content: "";
        position: absolute;
        width: 10px;
        height: 10px;
        border-left: 2px solid hsl(120, 0%, 40%);
        border-bottom: 2px solid hsl(120, 0%, 40%);
        background: hsl(120, 0%, 95%);
        transform: translate(0.5rem, -100%) rotate(-45deg);
      }
      &:hover::after {
        content: attr(data-tooltip);
        opacity: 1;
        position: absolute;
        width: 40vw;
        left: -0.8rem;
        transform: translate(10px, -110%);
        background-color: hsl(120, 0%, 95%);
        box-shadow: 1px 0px 0px hsl(120deg, 40%, 90%);
      }
    }
  }
  & > .pref-item {
    padding: 1.2rem 0;
    margin: 0 0.2rem;
  }
  & > .locate-image-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-auto-rows: minmax(100px, 33%);
    grid-gap: 1rem;
    min-height: 500px;
    transition: all 0.5s ease-in;
    & > .locate-selection {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      border: solid 5px #eeeeee;
      border-radius: 10px;
      cursor: pointer;
      & > div {
        box-sizing: border-box;
        border: solid 5px #bababa;
        border-radius: 5px;
        width: 50%;
        height: 50%;
      }
    }
    & > .selected {
      border: solid 5px #98b8f6;
      box-shadow: 0px 0px 5px 1px #7198e1;
    }
  }
}

.position-1 {
  position: absolute;
  top: 0;
  left: 0;
}
.position-2 {
  position: absolute;
  top: 0;
}
.position-3 {
  position: absolute;
  top: 0;
  right: 0;
}
.position-4 {
  position: absolute;
  left: 0;
}
.position-5 {
  position: absolute;
}
.position-6 {
  position: absolute;
  right: 0;
}
.position-7 {
  position: absolute;
  bottom: 0;
  left: 0;
}
.position-8 {
  position: absolute;
  bottom: 0;
}
.position-9 {
  position: absolute;
  bottom: 0;
  right: 0;
}


.bg-img {
  max-width: 100vw;
  max-height: 100vh;
  position: absolute;
}

.color-history {
  height: 200px;
  width: 100vw;
  display: flex;
  flex-direction: row;
  overflow-x: scroll;

  & > .color-item {
    display: flex;
    width: 200px;
    flex: none;
    flex-direction: column;
    justify-content: center;
  }
}

input[type="range"] {
  -webkit-appearance: none;
  appearance: none;
  background-color: #c7c7c7;
  height: 2px;
  width: 95vw;

  &:focus,
  &:active {
    outline: none;
  }

  &::-moz-range-thumb,
  &::-ms-thumb,
  &::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    cursor: pointer;
    position: relative;
    border: none;
    width: 12px;
    height: 12px;
    display: block;
    background-color: #262626;
    border-radius: 50%;
    -webkit-border-radius: 50%;
  }
}

input[type="file"] {
  & + label {
    outline: none;
    border: 2px solid hsl(120, 0%, 40%);
    color: black;
    padding: 5px;
    margin: 1rem;
    display: inline-block;
    transition: all 0.1s ease-out;
    &:hover {
      background-color: hsl(120, 0%, 30%);
      color: white;
    }
  }
}

.visually-hidden {
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
}

/* 互換性のための別ルールとして、最近の Firefox と Chrome では :focus-within が必要です。 */
input.visually-hidden:focus + label {
  outline: thin dotted;
}
input.visually-hidden:focus-within + label {
  outline: thin dotted;
}

.specify-imgs {
  & > .clear-btn {
    outline: none;
    border: 2px solid hsl(120, 0%, 40%);
    background-color: rgb(255, 251, 200);
    color: black;
    padding: 5px;
    margin: 1rem;
    display: inline-block;
    transition: all 0.1s ease-out;
    &:hover {
      border: 2px solid rgb(238, 255, 0);
      background-color: rgb(255, 246, 127);
    }
  }
}